<script>
import axios from 'axios'
export default {
  data () {
    return {
      from: true,
      paifangzishow: true,
      hetongzhuangtaishow: true,
      huikuanshow: true,
      shouziliaoshow: true,
      paibanzishow: true,
      disabledshow: false,
      maxImgUrl: '', // url
      centerDialogVisible: false, // Dialog 
      collapsed: false,
      dialogVisible: false, // 
      downloadDialog: false, // 
      dialogMessage: '', // 
      imgVisible: false,
      activeName: '0',
      contractProgressList: [], // 
      payList: [], // 
      updated_at1: '', // 
      details: { // 
        user_name: '', // 
        cst_name: '', //  
        cst_phone: '', //  
        cst_address: '', //  
        cst_remark: '', //  
        zj_input_format: '', //  
        zj_input_capacity: '', // 
        zj_input_num: '', //  
        zj_unit_price: '', //  
        zj_price: '', //  
        pay_sum: '', //  
        updated_at: '', //  
        scd_name: '', //  
        rf_remark: '', // 
        h_remark: '', // 
        d_remark: '', // 
        cst_latitude: '', // 
        cst_longitude: '', // 
        h_face: '' // 
      }
    }
  },
  methods: {
    //  tab 
    handleClick (tab, event) {
      console.log('tab', tab.name)
      if (tab.name === '0') {
        this.initData()
      } else if (tab.name === '3') {
        this.initData()
        if (this.details.h_is_finish === 0) {
          // this.dialogVisible = true
          // this.dialogMessage = ''
          this.showWarningTips('')
          this.paifangzishow = false
        }
      } else if (tab.name === '4') {
        this.initData()
        if (this.details.d_is_finish === 0) {
          // this.dialogVisible = true
          // this.dialogMessage = ''
          this.showWarningTips('')
          this.shouziliaoshow = false
        }
      } else if (tab.name === '5') {
        this.initData()
        if (this.details.rf_is_finish === 0) {
          // this.dialogVisible = true
          // this.dialogMessage = ''
          this.showWarningTips('')
          this.paibanzishow = false
        }
      } else if (tab.name === '1') {
        let planId = this.$route.query.planId
        console.log('id', planId)
        if (planId) {
          // 
          axios.get('/api/pc/customerDataPc/contractStatus/' + planId).then(resp => {
            if (resp.length === 0) {
              this.hetongzhuangtaishow = false
              this.showWarningTips('')
            }
            this.contractProgressList = []
            for (let i = 0; i < resp.length; i++) {
              let contractProgress = {
                scd_name: resp[i].scd_name, // 
                from_name: resp[i].from_name, // 
                scd_remark: resp[i].scd_remark, // 
                scd_r_remark: resp[i].scd_r_remark, // 
                updated_at: resp[i].updateTime // 
              }
              this.contractProgressList.push(contractProgress)
            }
          })
        }
      } else {
        let planId = this.$route.query.planId
        console.log('id', planId)
        if (planId) {
          // 
          axios.get('/api/pc/customerDataPc/payStatus/' + planId).then(resp => {
            if (resp.length === 0) {
              // this.dialogVisible = true
              // this.dialogMessage = ''
              this.huikuanshow = false
              this.showWarningTips('')
            }
            this.payList = []
            for (let i = 0; i < resp.length; i++) {
              let payProgress = {
                name: resp[i].name, // 
                pay_money: resp[i].pay_money, // 
                pay_remark: resp[i].pay_remark, // 
                pay_time: resp[i].pay_time.slice(0, 10)// 
              }
              this.payList.push(payProgress)
            }
          })
        }
      }
    },
    //  
    initData () {
      let planId = this.$route.query.planId
      if (planId) {
        axios.get('/api/pc/customerDataPc/planDetail/' + planId).then(resp => {
          this.updated_at1 = resp.updateTime
          console.log('7777777777', this.updated_at1)
          this.details = resp
          console.log('99999', this.details)
        })
      }
    },
    // 
    showMax (url) {
      console.log('url===>>', url)
      this.centerDialogVisible = true
      this.maxImgUrl = url
    },
    showMax1 () {
      this.centerDialogVisible = false
    },
    download () {
      if (this.details.d_is_finish === 0 && this.details.h_is_finish === 0) {
        this.dialogVisible = true
        this.dialogMessage = ''
        this.disabledshow = true
      } else {
        this.downloadDialog = true
        this.dialogMessage = ''
        this.disabledshow = false
      }
    },
    downLoadData () {
      this.downloadDialog = false
      let shortUrl = this.details.short_url
      console.log('666666', shortUrl)
      if (!shortUrl) {
        this.$message.error('')
        // this.$router.push({path: '/download', query: {shortUrl: ''}})
        window.open('#/download?shortUrl=' + shortUrl) // 
      }
      window.open('#/download?shortUrl=' + shortUrl) // 
    },
    showWarningTips (text) {
      this.$message({
        message: text,
        type: 'warning'
      })
    }
  },
  mounted () {
    if (this.$route.query.from === 1) {
      this.from = true
    } else {
      this.from = false
    }
    this.initData()
  }
}
