<template>
  <el-card class="box-card">
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/CustomerData' }"></el-breadcrumb-item>
    </el-breadcrumb>
    <el-row>
      <el-row class="f-m">
        <el-col :span="24" style="margin-top: 20px;" class="clearfix">
          <el-col :span="8" class="y-Center">
            <el-col :span="6" class="font-right"></el-col >
            <el-col :span="18">
              <el-select @change="tjzqChange" v-model="tjzqvalue" size="small">
                <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-col>
          </el-col>
          <el-col :span="8" class="y-Center">
            <el-col :span="6" class="grid-content bg-purple font-right"></el-col>
            <el-col :span="18">
              <el-date-picker unlink-panels @change="selectdateChange" value-format="yyyy-MM-dd" style="width: 88%" size="small" v-model="datevalue" type="daterange" range-separator="" start-placeholder="" end-placeholder="">
              </el-date-picker>
            </el-col>
          </el-col>
        </el-col>
        <el-col :span="24" style="margin-top: 20px;" class="clearfix">
          <el-col :span="8" class="y-Center">
            <el-col :span="6" class="font-right"></el-col>
            <el-col :span="18">
              <el-select @change="tdfwChange" size="small" class="fl" v-model="tdfwvalue">
                <el-option v-for="item in tdfwoptions" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-col>
          </el-col>
          <el-col :span="8" class="y-Center">
            <el-col :span="6" class="font-right"></el-col>
            <el-col :span="18">
              <el-select @change="teannameChange" size="small" style="width: 88%" :disabled="teannameshow" class="fl" v-model="teamname">
                <el-option v-for="(item, index) in teamoptions" :key="index" :label="item.name" :value="item.id">
                </el-option>
              </el-select>
            </el-col>
          </el-col>
          <el-col :span="8" class="y-Center">
            <el-col :span="6" class="font-right"></el-col>
            <el-select size="small" v-model="fuzerenvalue" @change="fuzerenChange" :disabled="fuzerenshow">
              <el-option v-for="(item, index) in fuzerenoptions" :key="index" :label="item.name" :value="item.openid">
              </el-option>
            </el-select>
          </el-col>
        </el-col>
        <el-col :span="24" style="margin-top: 20px;" class="clearfix">
          <el-col :span="8" class="y-Center">
            <el-col :span="6" class="font-right"></el-col>
            <el-select size="small" class="fl" @change="contractChange" v-model="contractvalue" placeholder="">
              <el-option v-for="item in contractoptions" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="8" class="y-Center">
            <el-col :span="6" class="font-right"></el-col>
            <el-col :span="18">
              <el-select style="width: 88%" size="small" @change="overdueChange" v-model="overduevalue" placeholder="">
                <el-option v-for="item in overdueoptions" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-col>
          </el-col>
        </el-col>
        <el-col :span="24" style="margin-top: 20px;">
          <el-col :span="16" class="y-Center">
            <el-col :span="3" class="font-right"></el-col>
            <el-col :span="21">
              <el-col>
                <el-input style="width: 95%;" size="small" @input="searchChange" placeholder="" prefix-icon="el-icon-search" v-model="searchvalue">
                </el-input>
              </el-col>
            </el-col>
          </el-col>
        </el-col>
      </el-row>
    </el-row>
    <el-row>
      <el-col :span="24">
        <el-table :data="tableData" size="small" stripe
                  v-loading="tableLoading"
                  element-loading-text="..."
                  element-loading-spinner="el-icon-loading"
                  style="width: 100%;border: 1px solid #ebeef5;margin-top: 30px;">
          <el-table-column prop="cst_name" label="" ></el-table-column>
          <el-table-column prop="user_name" label="" ></el-table-column>
          <el-table-column prop="cst_address" :show-overflow-tooltip="showOverflowTooltip" label="" width="200"></el-table-column>
          <el-table-column prop="zj_input_capacity" label="" align="center"></el-table-column>
          <el-table-column prop="zj_price" label="" align="center"></el-table-column>
          <el-table-column :formatter="finishFormat" prop="h_is_finish" label="" align="center"></el-table-column>
          <el-table-column :formatter="finishFormat" prop="d_is_finish" label="" align="center"></el-table-column>
          <el-table-column :formatter="finishFormat" prop="rf_is_finish" label="" align="center"></el-table-column>
          <el-table-column prop="short_url" label="" width="70" align="center"></el-table-column>
          <el-table-column prop="createTime" label="" width="100" align="center"></el-table-column>
          <el-table-column fixed="right" label="" width="100"  align="center">
            <template slot-scope="scope">
              <el-button @click="handleClick(scope.row.id)" type="text" size="small" ></el-button>
              <el-button :disabled="scope.row.h_is_finish == 0 && scope.row.d_is_finish == 0" type="text" @click="downLoadData(scope.row.short_url)" size="small"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <el-pagination style="margin-top: 20px;"
                   @size-change="handleSizeChange"
                   @current-change="handleCurrentChange"
                   :current-page="currentPage4"
                   :page-sizes="[10, 20, 30, 40,50]"
                   :page-size="100"
                   layout="total, sizes, prev, pager, next, jumper"
                   :total="totalNum">
    </el-pagination>
  </el-card>
</template>
<script>
import axios from 'axios'
import values from '../../utils/values'
export default {
  data () {
    return {
      disabled: true,
      showOverflowTooltip: true,
      tableLoading: false,
      teamLevel: 'all',
      teamId: 'all',
      planOwner: 'all',
      tjzqvalue: '',
      tdfwvalue: '()',
      fuzerenvalue: '()',
      teamname: '()',
      teannameshow: true,
      fuzerenshow: true,
      teamoptions: [],
      teamoptionsAll: [],
      fuzerenoptions: [],
      fuzerenoptionsAll: [],
      options: [{
        value: 'today',
        label: ''
      }, {
        value: 'yesterday',
        label: ''
      }, {
        value: 'thisWeek',
        label: ''
      }, {
        value: 'lastWeek',
        label: ''
      }, {
        value: 'thisMonth',
        label: ''
      }, {
        value: 'lastMonth',
        label: ''
      }, {
        value: 'thisYear',
        label: ''
      }, {
        value: 'total',
        label: ''
      }, {
        value: '',
        label: ''
      }],
      tdfwoptions: [
        {
          value: 'all',
          label: '()'
        },
        {
          value: 1,
          label: ''
        },
        {
          value: 2,
          label: ''
        },
        {
          value: 3,
          label: ''
        },
        {
          value: 'one',
          label: ''
        }
      ],
      tableData: [],
      datevalue: [],
      contractoptions: [
        {
          value: 'all',
          label: '()'
        },
        {
          value: '0',
          label: ''
        },
        {
          value: '2',
          label: ''
        },
        {
          value: '3',
          label: ''
        },
        {
          value: '4',
          label: ''
        },
        {
          value: '6',
          label: ''
        }
      ],
      overdueoptions: [
        {
          value: 'all',
          label: '()'
        },
        {
          value: '1',
          label: ''
        },
        {
          value: '0',
          label: ''
        }
      ],

      contractvalue: '()',
      overduevalue: '()',
      searchvalue: '',
      currentPage4: 1,
      totalNum: 0,
      scdStatus: 'all',
      overDueStatus: 'all',
      pagesizeNum: 10,
      pageNum: 1
    }
  },
  methods: {
    finishFormat (row, column, cellValue, index) {
      return cellValue === 0 ? '' : ''
    },
    handleSizeChange (val) {
      console.log(` ${val} `)
      this.pagesizeNum = val
      this.formlistdata()
    },
    handleCurrentChange (val) {
      console.log(`: ${val}`)
      this.pageNum = val
      this.formlistdata()
    },
    tjzqChange (e, isExcute) {
      this.datevalue = []
      this.pageNum = 1
      this.tjzqvalue = e
      console.log('===>>', e, this.tjzqvalue)
      if (this.tjzqvalue !== '') {
        axios.get('/api/pc/select/date/' + e).then(res => {
          console.log(res)
          for (let i in res) {
            this.datevalue.push(res[i])
          }
          if (!isExcute) {
            this.formlistdata()
          }
        })
      } else {
        if (!isExcute) {
          this.formlistdata()
        }
      }
    },
    selectdateChange (e) {
      this.pageNum = 1
      this.tjzqvalue = ''
      this.datevalue = e
      console.log('', this.datevalue)
      this.formlistdata()
    },
    tdfwChange (e, openid, fn) {
      this.pageNum = 1
      console.log('', e)
      this.teamLevel = e
      this.tdfwvalue = e
      this.teamId = 'all'
      this.teamoptions = [
        {
          name: '()'
        }
      ]
      for (let i = 0; i < this.teamoptionsAll.length; i++) {
        if (e === 'all') {
          this.planOwner = 'all'
          this.fuzerenvalue = '()'
          this.teamoptions.push(this.teamoptionsAll[i])
          this.teannameshow = true
          this.fuzerenshow = true
        }
        if (e === Number(this.teamoptionsAll[i].level)) {
          this.fuzerenvalue = '()'
          this.planOwner = 'all'
          this.teannameshow = false
          this.fuzerenshow = true
          this.teamoptions.push(this.teamoptionsAll[i])
          console.log('=====', this.teamoptionsAll[i])
        }
        this.teamname = this.teamoptions[0].name
      }
      if (e === 'one') {
        this.planOwner = this.fuzerenID
        this.teannameshow = true
        this.teamoptions = [
          {
            name: '',
            id: 'one'
          }
        ]
        this.fuzerenoptions = []
        this.teamname = this.teamoptions[0].name
        for (let i = 0; i < this.fuzerenoptionsAll.length; i++) {
          if (String(e) === String(this.fuzerenoptionsAll[i].team_id)) {
            this.fuzerenoptions.push(this.fuzerenoptionsAll[i])
            this.fuzerenshow = false
            console.log('', this.fuzerenshow)
          }
        }
        this.fuzerenvalue = this.fuzerenoptions[0].name
        this.planOwner = this.fuzerenoptions[0].openid
      }

      if (openid) {
        if (fn) {
          this.fuzerenChange(openid, fn)
        } else {
          this.fuzerenChange(openid)
        }
      } else {
        this.formlistdata()
      }
    },
    teannameChange (e) {
      this.pageNum = 1
      console.log('ID', e)
      this.teamId = e
      this.fuzerenoptions = [
        {
          name: '()'
        }
      ]
      if (!e) {
        this.fuzerenshow = true
        this.teamId = 'all'
      }
      this.fuzerenvalue = this.fuzerenoptions[0].name
      this.planOwner = 'all'
      for (let i = 0; i < this.fuzerenoptionsAll.length; i++) {
        if (e === Number(this.fuzerenoptionsAll[i].team_id)) {
          this.fuzerenoptions.push(this.fuzerenoptionsAll[i])
          this.fuzerenshow = false
          console.log('', this.fuzerenshow)
        }
      }
      this.formlistdata()
    },
    requestdata (fn, voidGetDate, openid) {
      if (!voidGetDate) {
        if (openid) {
          this.getDateData(fn, openid)
        } else {
          this.getDateData(fn)
        }
      } else {
        if (openid) {
          this.getTeamData(fn, openid)
        } else {
          this.getTeamData(fn)
        }
      }
    },
    getDateData (fn, openid) {
      axios.get('/api/pc/select/date/' + 'today').then(res => {
        this.datevalue.push(res.beginDate, res.endDate)
        console.log('', this.datevalue)
        this.getTeamData(fn, openid)
      })
    },
    getTeamData (fn, openid) {
      axios.get('/api/pc/select/team').then(res => {
        console.log('', res)
        res.teams.forEach(item => {
          this.teamoptionsAll.push(item)
          this.teamoptions.push(item)
        })
        res.agents.forEach(item => {
          this.fuzerenoptions.push(item)
          this.fuzerenoptionsAll.push(item)
        })
        console.log('', this.teamoptions)
        console.log('', this.fuzerenoptions)
        if (openid) {
          this.tdfwChange('one', openid, fn)
        } else {
          if (fn) {
            fn()
          }
        }
      })
    },
    fuzerenChange (e, fn) {
      this.pageNum = 1
      this.fuzerenID = e
      console.log('sdjkfdsjfjdsjfdjs', e)
      this.planOwner = e
      if (!e) {
        this.planOwner = 'all'
      }
      if (fn) {
        fn()
      } else {
        this.formlistdata()
      }
    },
    contractChange (e) {
      this.pageNum = 1
      this.scdStatus = e
      console.log(this.scdStatus)
      this.formlistdata()
    },
    overdueChange (e) {
      this.pageNum = 1
      this.overDueStatus = e
      this.formlistdata()
    },
    formlistdata () {
      this.tableLoading = true
      setTimeout(() => {
        console.log('', this.datevalue[0])
        let parameter = {
          beginDate: this.datevalue[0],
          endDate: this.datevalue[1],
          teamLevel: String(this.teamLevel),
          teamId: String(this.teamId),
          planOwner: this.planOwner,
          scdStatus: this.scdStatus,
          overDueStatus: this.overDueStatus,
          pageNumber: this.pageNum,
          pageSize: this.pagesizeNum,
          search: this.searchvalue,
          type: 'customer'
        }
        axios.post('/api/pc/planSchedulePc', parameter).then(res => {
          this.tableLoading = false
          this.tableData = res.content
          console.log('11111111111111111111', this.tableData)
          this.totalNum = res.totalCount
          for (let i = 0; i < this.tableData.length; i++) {
            this.tableData[i].createTime = this.tableData[i].createTime.slice(0, 10)
          }
          console.log('11111111111111111111', this.tableData, res)
        }, () => {
          this.tableLoading = false
        }).catch(() => {
          this.tableLoading = false
        })
      }, 300)
    },
    searchChange () {
      this.formlistdata()
    },
    handleClick (id) {
      console.log('id:')
      console.log(id)
      this.$router.push({path: '/CustomerDetails', query: {planId: id, from: 2}})
    },
    downLoadData (url) {
      console.log('777777', url)
      if (!url) {
        this.$message.error('')
        // this.$router.push({path: '/download', query: {url: ''}})
        window.open('#/download?shortUrl=' + url) // 
      }
      window.open('#/download?shortUrl=' + url)
    }
  },
  mounted () {
    let sessionUser = JSON.parse(sessionStorage.getItem(values.storage.user)) || {}
    console.log('user', sessionUser)
    let params = this.$route.query
    console.log('===>>', params)
    if (Object.keys(params).length !== 0) {
      this.requestdata(this.tjzqChange('total', false), true, params.openid)
    } else {
      this.requestdata(this.formlistdata)
    }
  }
}
</script>
<style>
  .fl{
    float: left;
  }
  .fr{
    float: right;
  }
  .clearfix:after {
    content: "";
    display: block;
    visibility: hidden;
    height: 0;
    clear: both;
  }
  .clearfix {
    zoom: 1;
  }
  .x-Center{
    display: flex;
    display: -webkit-flex;
    -webkit-justify-content: center;
    justify-content: center;
  }
  .xy-Center{
    display: flex;
    display: -webkit-flex;
    -webkit-align-items: center;
    align-items: center;
    -webkit-justify-content: center;
    justify-content: center;
  }
  .y-Center{
    display: flex;
    display: -webkit-flex;
    -webkit-align-items: center;
    align-items: center;
  }
  .el-date-editor .el-range-separator {
    width: 10%;
  }
  .el-card__body {
    padding: 20px !important;
  }
  .el-table td {
    border-right: 1px solid #ebeef5 !important;
  }
  .el-table th.is-leaf {
    border-right: 1px solid #ebeef5 !important;
  }
  .one-txt-cut{
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .has-gutter>tr{
    background: #f5f8f8;
  }
</style>



